.header{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.headerText{
    color: white;
    text-align: center;
    font-family: Helvetica, sans-serif;
    font-size: 4rem;
    position: center;
}

.info{
    height: 10vh;
    background-color: white;
    margin: 2vw;
    padding: 2rem;
    border-radius: 0.5rem;

}

.downButton{
    color: white;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 0.75rem;
    padding: 0.5rem;
    position: fixed;
    bottom: 2rem;
}

.bounce{
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}

.animateUp{
    -webkit-animation: animateUp 0.5s ease-in-out;
    animation: animateUp 0.5s ease-in-out;
}

.site_background {
    margin: 0rem;
    position: center;
    padding: 0.25rem 1rem 8rem;
    border-radius: 1rem;
    background-color: white;
}

@-webkit-keyframes animateUp {
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
        opacity: 0;

    }
}

@keyframes animateUp {
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
        opacity: 0;

    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}





.infoHeader{
    font-size: 3rem;
    font-weight: bolder;
    color: #2b2b2b;
}

.profile_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-grow: 1;
    flex-wrap: wrap;

}

.profile {
    margin: 2rem;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body{
  height: 100%;
  background: #23A6D5 -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background: #23A6D5 linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background-size: 200% 200%;
  -webkit-animation: Gradient 20s ease infinite;
  animation: Gradient 20s ease infinite;
}



@-webkit-keyframes Gradient {
  0% {
    background-position: 0 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0 50%
  }
}

@keyframes Gradient {
  0% {
    background-position: 0 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0 50%
  }
}


